.container {
	width: 85%;
	max-width: 1160px;
	@include PC {
		width: 100%;
		max-width: 95%;
	}
	@include mobile-md {
		max-width: 98%;
	}
	&-fluid {
		padding: 0 3%;
	}
}
.main {
	padding-top: 170px;
    @include PC-md{
        padding-top: 154px;
    }
	@include PC{
		padding-top: 151px;
	}
	@include PC-sm{
		padding-top: 109px;
	}
    @include Pad{
        padding-top: 44px;
    }
	@include mobile-md {
		padding-top: 63px;
	}
}
.section {
	position: relative;
	z-index: 20;
	&-text {
		@include Pad {
			font-size: $h8-font-size;
		}
	}
	&[data-con="index"]{
		margin-top: -76px;
		@include PC-md{
			margin-top: -48px;
		}
		@include Pad{
			margin-top: -32px;
		}
		@include mobile-lg{
			margin-top: -24px;
		}
	}
}
.basic-btn {
	display: flex;
	justify-content: center;
	border: 2px solid $primary;
	border-radius: 60px;
	color: $white;
	font-size: $h7-font-size;
	font-weight: $font-weight-medium;
	background: $primary;
	padding: 0;
	&:hover {
		color: $primary;
		border: 2px solid $primary;
		background: $white;
		img {
			filter: invert(37%) sepia(51%) saturate(758%) hue-rotate(351deg) brightness(70%) contrast(97%);
		}
	}
	img {
		width: 16px;
	}
	@include Pad {
		font-size: $h8-font-size;
	}
}
.cancel-btn {
	display: flex;
	justify-content: center;
	border: 2px solid $gray-600;
	border-radius: 60px;
	color: $white;
	font-size: $h7-font-size;
	font-weight: $font-weight-medium;
	background: $gray-600;
	padding: 0;
	&:hover {
		color: $gray-600;
		border: 2px solid $gray-600;
		background: $white;
		img {
			filter: invert(37%) sepia(51%) saturate(758%) hue-rotate(351deg) brightness(70%) contrast(97%);
		}
	}
	img {
		width: 16px;
	}
	@include Pad {
		font-size: $h8-font-size;
	}
}
/*--------------------主選單開始------------------*/
.navbar {
	top: 122px;
	@include PC-md{
		top: 106px;
	}
	@include PC-sm{
		top: 64px;
	}
	&-top {
		display: flex;
		align-items: end;
		justify-content: space-between;
		background: $secondary-xlight;
		padding-top: 32px;
		padding-bottom: 32px;
		@include PC-md{
			padding-top: 24px;
			padding-bottom: 24px;
		}
		@include PC-sm{
			padding-top: 12px;
			padding-bottom: 12px;
		}
		@include Pad{
			padding-top: 8px;
			padding-bottom: 8px;
		}
	}
	&-brand {
		display: flex;
		align-items: end;
		@include mobile-md{
			flex-direction: column;
			align-items: start;
		}
	}
	&-logo {
		margin-right: 12px;
		height: 58px;
		@include PC-sm{
			height: 40px;
		}
		@include Pad{
			height: 24px;
			margin-bottom: 4px;
		}
	}
	&-title {
		color: $primary;
		font-size: $h4-font-size;
		margin-bottom: 8px;
		font-weight: $font-weight-bold;
		@include PC{
			font-size: $h5-font-size;
		}
		@include PC-sm{
			font-size: $h6-font-size;
		}
	}
	&-input {
		max-width: 360px;
		width: 100%;
		height: 40px;
		margin-left: 24px;
		@include PC-md{
			max-width: 280px;
		}
		@include PC-sm{
			max-width: 260px;
		}
		@include Pad{
			max-width: 300px;
			margin: 8px auto;
		}
		.form-control {
			border: 1px solid $secondary !important;
			border-radius: 50px 0 0 50px !important;
			border-right: none !important;
		}
		.btn {
			background: $white;
			border: 1px solid $secondary !important;
			border-radius: 0 50px 50px 0 !important;
			border-left: none !important;
		}
	}
	&-toggler {
		border: none;
		width: 30px;
		height: 30px;
		@include text-indent;
		background: url("../images/ic-close.svg") no-repeat center;
		background-size: 22px;
		&.collapsed {
			background: url("../images/ic-bar.svg") no-repeat center;
			background-size: 24px;
		}
		@include Pad{
			position: fixed;
			right: 32px;
			top: 8px;
		}
		@include mobile-md{
			right: 12px;
			top: 16px;
		}
	}
	&-collapse {
		@include Pad {
			background: $secondary;
			box-shadow: 0 4px 3px $gray-500;
			position: fixed;
			top: 44px;
			left: 0;
			width: 100%;
		}
		@include mobile-md{
			top: 63px;
		}
	}
}

.nav {
	&-item {
		display: flex;
		align-items: center;
		position: relative;
		@include PC {
			font-size: $h7-font-size;
		}
		@include Pad {
			flex-direction: column;
			border-bottom: 1px solid $secondary-xlight;
			&:last-child{
				border-bottom: none;
			}
		}
		&:hover {
			.nav-link {
				color: $primary;
				background: $secondary;
			}
		}
	}
	&-link {
		display: block;
		color: $white !important;
		padding: 12px 40px !important;
		position: relative;
		font-weight: $font-weight-medium;
		@include PC {
			padding: 12px 16px !important;
		}
		@include Pad {
			width: 100%;
			padding: 12px 16px !important;
			text-align: center;
		}
	}
}

/*--------------------footer開始------------------*/
.footer {
	position: relative;
	font-size: $h7-font-size;
	letter-spacing: 1px;
	padding-top: 56px;
	@include mobile-lg {
		font-size: $h8-font-size;
		padding-top: 48px;
		letter-spacing: 0;
	}
	&-logo {
		width: 195px;
	}
	&-function {
		display: flex;
		flex-direction: column;
		position: fixed;
		right: 16px;
		bottom: 200px;
		z-index: 100;
		@include PC-lg{
			bottom: 160px;
		}
		@include mobile-lg{
			bottom: 200px;
		}
		&-link {
			width: 55px;
			height: 55px;
			border-radius: 10px;
			color: $white;
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			background: $primary;
			font-weight: $font-weight-bold;
			font-size: $h6-font-size;	
			&:hover{
				&:before{
					top: -36px;
				}
			}
			&:before{
				transition: all 0.2s ease;
				content: "";
				width: 15px;
				height: 40px;
				position: absolute;
				background: url("../images/ic-up.svg") no-repeat;
				background-size: cover;
				top: -30px;
				right: 12px;
			}
			@include mobile-lg {
				width: 48px;
				height: 48px;
			}
		}
	}
}

/*--------------------視窗開始------------------*/
.backdrop {
	width: 100vw;
	height: 100vh;
	position: absolute;
	left: 0;
	top: 0;
	display: none;
	z-index: 100;
	&.show {
		display: block;
	}
}
.offcanvas-backdrop.show {
	opacity: 0.7;
}
.offcanvas-header {
	padding-left: 20px;
	padding-right: 32px;
	.btn {
		&:active,
		&:focus {
			border: 0;
		}
	}
}
.offcanvas-bottom {
	border-radius: 24px 24px 0 0;
	height: 350px !important;
	padding: 24px 0;
	.offcanvas-header {
		position: relative;
		&-close {
			position: absolute;
			background: $gray-100 !important;
			border-radius: 24px 24px 0 0;
			width: 106px;
			height: 40px;
			top: -39px;
			left: calc(50% - 53px);
			display: flex;
			justify-content: center;
			align-items: center;
			border: none;
			&::before,
			&::after {
				content: "";
				display: block;
				position: absolute;
				width: 24px;
				height: 24px;
				background: url("../images/fillet-gray.svg");
			}
			&::before {
				bottom: -1px;
				left: -23px;
			}
			&::after {
				bottom: -1px;
				right: -23px;
				transform: rotate(90deg);
			}
			&:focus {
				background: $gray-100;
			}
		}
	}
	.offcanvas-body {
		padding: 0 66px;
		overflow-y: initial;
	}
}
.offcanvas-end {
	width: 350px !important;
	border-radius: 24px 0 0 24px;
	.offcanvas-header {
		border-radius: 24px 0 0 0;
	}
}
.offcanvas-body {
	padding: 0 32px 24px 32px;
}

.select-list-wrapper {
	overflow-y: auto;
	padding: 0 32px 24px 32px;
	flex: 1 1;
	font-size: $h6-font-size;
	.form-check-input {
		width: 16px;
		height: 16px;
		margin-top: 0;
		border: 2px solid $gray-700 !important;
		&[type="radio"] {
			border-radius: 50%;
		}
		&:checked[type="radio"] {
			border-radius: 50%;
			background: url(../images/radio-circle.svg) no-repeat center $primary;
			background-size: 6px;
			border: 2px solid $primary !important;
		}
		&[type="checkbox"] {
			border-radius: 4;
		}
		&:checked[type="checkbox"] {
			background: url(../images/ic-check.svg) no-repeat center $primary;
			background-size: 9px;
			border: 2px solid $primary !important;
		}
	}
	input[disabled] {
		border: 2px solid $gray-600;
	}
	input[disabled] ~ label {
		opacity: 1 !important;
		color: $gray-700;
	}
	input[disabled] ~ label span {
		color: $danger !important;
	}
	label {
		display: flex;
		justify-content: space-between;
		padding-left: 4;
		p {
			display: block;
			width: calc(100% - 70px);
			flex: 1 1;
			line-height: 1.3;
		}
		span {
			padding-left: 8px;
		}
	}
}
