/*--------------------首頁開始------------------*/
.banner {
    //紅眼banner
    position: relative;
    &:before{
        content: "";
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 40px;
        background: linear-gradient(to bottom,  rgba($black,0) 0%,rgba($black,.4) 100%); 
        z-index: 10;
    }
    &-item {
        position: relative;
        z-index: 1;
        &-link {
            display: flex;
            justify-content: center;
            transition: all 0.2s ease;
            overflow: hidden;
        }
        img {
            transition: all 0.2s ease;
        }
    }
}
.link-box{
    border-radius: 20px;
    padding: 24px;
    background: $white;
    box-shadow: rgba($black, 0.16) 0px 4px 6px;
    &-link{
        display: flex;
        align-items: center;
        padding: 20px 29px;
        border: 1px solid $secondary-light;
        border-radius: 16px;
        @include Pad{
            padding: 16px 24px;
       }
        &:hover{
            background: $gray-100;
        }
    }
    &-icon{
        width: 50px;
        height: 50px;
        border-radius: 20px 0 20px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 16px;
        @include Pad{
            width: 40px;
            height: 40px;
        }
        img{
            width: 60%;
        }
    }
    &-text{
        font-size: $h5-font-size;
        font-weight: $font-weight-bold;
        @include Pad{
            font-size: $h6-font-size;
        }
    }

}
.keywords {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    @include Pad {
        flex-wrap: wrap;
    }
    &-title {
        @include Pad {
            width: 100%;
        }
    }
    &-list {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        @include Pad {
            width: 100%;
        }
        &:before {
            content: "";
            display: block;
            width: 14px;
            height: 12px;
            background: url("../images/ic-arrow-right.svg") no-repeat;
            background-size: contain;
            margin-right: 16px;
            @include PC-sm {
                margin-right: 0;
            }
        }
    }
    &-item {
        border-right: 1px solid $primary-light;
        &:last-child {
            border: none;
        }
    }
    &-link {
        color: $primary;
        padding: 0 24px;
        @include PC-sm {
            padding: 0 16px;
            font-size: $h7-font-size;
        }
        @include mobile-lg {
            padding: 0 12px;
            font-size: $h8-font-size;
        }
        @include mobile-md {
            padding: 0 10px;
        }
        &:hover {
            color: $primary-dark;
        }
    }
    &-btn {
        @include Pad {
            position: absolute;
            right: 12px;
            top: 16px;
            font-size: $h7-font-size;
            img {
                width: 20px;
            }
        }
    }
}
.main-list {
    &[data-size="lg"] {
        .main-list-btn {
            img {
                @include Pad {
                    width: 48px;
                }
            }
        }
    }
    &[data-con="multi-btn"] {
        .main-list-btn {
            flex-direction: row;
            align-items: center;
            padding: 32px;
            text-align: left;
            height: 145px;
            justify-content: flex-start;
            @include PC-sm {
                height: 90px !important;
            }
            img {
                width: 50px;
                @include PC-sm {
                    height: 30px;
                }
            }
            p {
                color: $black;
                font-size: $h6-font-size;
                font-weight: $font-weight-medium;
                @include over_text(1);
                @include PC-sm {
                    font-size: $h7-font-size;
                }
            }
        }
    }
    &-btn {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: $white !important;
        border-radius: 16px;
        border: 1px solid $white;
        box-shadow: 0 5px 7px $gray-300;
        width: 100%;
        height: 220px;
        color: $primary-dark;
        @include PC-sm {
            flex-direction: row;
            height: 100px !important;
        }
        @include Pad {
            font-size: $h6-font-size;
        }
        &:hover {
            border: 1px solid $primary;
            box-shadow: none;
        }
        &-title {
            font-size: $h4-font-size;
            font-weight: $font-weight-semibold;
            @include PC-sm {
                font-size: $h5-font-size;
                margin-bottom: 0;
                width: 195px;
            }
            @include Pad {
                font-size: $h6-font-size;
                width: 165px;
            }
        }
        img {
            max-width: 45%;
            @include PC-sm {
                width: 52px;
            }
            @include Pad {
                max-width: 25%;
            }
            @include mobile-md {
                max-width: 40%;
            }
        }
    }
}
.btn-list {
    &-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        background: $white;
        border-radius: 16px;
        border: 1px solid $white !important;
        box-shadow: 0 5px 7px $gray-300;
        width: 100%;
        height: 150px;
        font-size: $h5-font-size;
        font-weight: $font-weight-medium;
        @include PC-sm {
            height: 100px !important;
        }
        @include Pad {
            font-size: $h6-font-size;
        }
        &:hover {
            border: 1px solid $primary !important;
            box-shadow: none;
        }
        img {
            width: 56px;
            @include PC-sm {
                width: 52px;
            }
            @include Pad {
                width: 48px;
            }
        }
    }
    p {
        padding: 0 16px;
        text-align: center;
        @include PC-sm {
            width: 195px;
        }
        @include Pad {
            width: 165px;
            padding: 0 8px;
        }
    }
}
