@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap');

/* --------------基本設定開始--------------- */
*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	width: 100%;
	-webkit-text-size-adjust: none;
}
html{
	&.sm {
		font-size: 0.9rem;
	}
	&.lg {
		font-size: 1.1rem;
	}
}
body {
	-webkit-font-smoothing: antialiased;
	-webkit-overflow-scrolling: touch;
	font-family: "Noto Sans TC", "Microsoft JhengHei", "sans-serif";
	line-height: 1.5;
	letter-spacing: 0.5px;
	color: $gray-900;
	/*避免在觸控時文字被選取*/
	-webkit-user-select:none;
	-webkit-touch-callout:none;
	-moz-user-select: none;
}
img {
	display: block;
	max-width: 100%;
	height: auto;
}
a,
a:hover,
button,
button:hover {
	transition: all 0.2s ease;
	text-decoration: none;
}
a:focus,button:focus{
	/*outline: 2px dashed $primary !important;*/
	box-shadow: none !important;
}
.btn{
	display: flex;
	justify-content: center;
	align-items: center;
	letter-spacing: 2px;
	font-weight: $font-weight-medium;
	border-radius: 12px;
}
hr{
	border-color: $base !important;
	opacity: 1 !important;
}
p {
	margin-bottom: 0;
}
ul {
	padding-left: 0;
	list-style: none;
	margin-bottom: 0;
}
ol {
	margin-bottom: 0;
	padding-left: 20px;
}
input[type="checkbox"]{
	border-radius: 2px !important;
	border: 1px solid $gray-800 !important;
}
.form-check-input:checked[type="checkbox"]{
	background: url("../images/ic-check.svg") no-repeat center;
	background-size: 8px;
}
.form-check-input[type="radio"]{
	border: 1px solid $gray-800 !important;
}
input,textarea{
	border-radius: 12px !important;
	border: 1px solid $gray-400 !important;
	@include Pad{
		font-size: $h7-font-size !important;
	}
}
select{
	border: 1px solid $gray-400 !important;
	background: url("../images/ic-select-down.svg") no-repeat right center $white !important;
	padding-right: 28px !important;
	border-radius: 12px !important;
	@include Pad{
		font-size: $h7-font-size !important;
	}
}
.select2-dropdown{
	border: none;
	box-shadow:  0 5px 5px $gray-600;
}
.select2-selection__arrow{
	background: url("../images/ic-select-down.svg") no-repeat center $white;
	height: calc(100% - 2px) !important;
	b{
		display: none;
	}
}

.select2-results__message{
	font-size: $h7-font-size;
	color: $primary;
	padding-left: 12px;
}
input::placeholder,textarea::placeholder {
	color: $gray-900!important;
	@include mobile-lg{
		font-size: $h7-font-size !important;
	}
}
/*無障礙設定*/
#gotocenter{
	display: flex;
	align-items: center;
	height: 50px;
	position: fixed;
    left: 10px;
	top: -100px;
	transition: all 0.2s ease;
	background: $primary;
    color: $white;
    font-weight: 700;
	z-index: 2000;
	border-radius: 0 0 10px 10px;
	padding: 10px;
	font-size: $h7-font-size;
	&:focus {
    top: 0;
}
}



input[readonly]{
	border: 1px solid $primary-light !important;
}
.form-check,select~label{
	@include mobile-lg{
		font-size: $h7-font-size !important;
	}
}

