.key {
    color: $gray-300;
    font-size: $h8-font-size;
}
/*-----------------標題樣式---------------------*/
.main-title {
    color: $black;
    font-size: $h2-font-size;
    font-weight: $font-weight-heavy;
    letter-spacing: 4px;
    text-align: center;
    background: url("../images/title-bg.svg") no-repeat;
    background-size: contain;
    margin: 0 auto;
    @include PC-md {
        font-size: $h3-font-size;
    }
    @include PC-sm {
        font-size: $h4-font-size;
    }
}
.sub-title {
    font-size: $h5-font-size;
    border-bottom: 1px solid $primary;
    font-weight: $font-weight-bold;
    padding-bottom: 8px;
    display: flex;
    align-items: center;
    @include Pad {
        font-size: $h6-font-size;
        line-height: 1.5;
    }
    img {
        width: 32px;
        margin-right: 8px;
    }
}
/*-----------------連結樣式---------------------*/
.link-img {
    display: inline-block;
    &:hover {
        opacity: 0.8;
    }
}
.link-list {
    border-left: 1px solid $primary-light;
    padding-left: 24px;
    @include Pad {
        padding-left: 16px;
    }
    &-link {
        display: block;
        padding: 8px 0;
        color: $black;
        font-weight: $font-weight-medium;
        &:hover {
            opacity: 0.8;
        }
    }
}
/*-----------------按鈕樣式---------------------*/
.tab {
    &-menu {
        display: flex;
        width: 165px;
        @include PC-sm {
            width: 100%;
        }
    }
    &-menu-btn {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        background: $gray-100;
        border: 1px solid $primary-light;
        color: $gray-700;
        border-radius: 0 !important;
        padding: 9px 12px;
        font-weight: $font-weight-semibold;
        font-size: $h5-font-size;
        @include Pad {
            flex-direction: column;
            font-size: $h6-font-size;
            padding-bottom: 15px;
        }
        &:hover {
            border: 1px solid $primary;
        }
        &.active {
            border: 1px solid $primary;
            border-left: 5px solid $primary;
            color: $primary;
            @include Pad {
                border: 1px solid $primary;
                padding-bottom: 15px;
                position: relative;
                &:after {
                    content: "";
                    display: block;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    height: 100%;
                    border: 2px solid $primary;
                    border-bottom-width: 5px;
                }
            }
            img {
                filter: invert(33%) sepia(95%) saturate(640%) hue-rotate(134deg) brightness(60%) contrast(102%);
            }
        }
        img {
            width: 24px;
        }
    }
    &-action {
        display: flex;
        justify-content: center;
        @include PC-sm {
            width: 100%;
        }
    }
}
@each $color, $value in $theme-colors {
    .tag-#{$color} {
        width: 50px;
        display: flex;
        justify-content: center;
        border: 1px solid $value;
        border-radius: 5px;
        color: $value;
        font-size: $h7-font-size;
        padding: 0;
        @include Pad {
            font-size: $h8-font-size;
        }
    }
}
@each $color, $value in $theme-colors {
    .link-underline-#{$color} {
        text-decoration: underline;
        color: $value;
        &:hover {
            text-decoration: none;
        }
    }
}
/*-----------------列表樣式---------------------*/
.tag-list {
    background: $white;
    &-item {
        border-bottom: 1px solid $primary-light;
        &:last-child {
            border-bottom: none;
        }
    }
    &-link {
        display: flex !important;
        align-items: start;
        color: $gray-800;
        padding: 16px 0;
        font-size: $h6-font-size;
        @include PC-sm {
            padding: 12px 0;
        }
        @include mobile-lg {
            font-size: $h7-font-size;
        }
        &:before {
            content: "";
            display: block;
            width: 7px;
            height: 17px;
            margin-right: 16px;
            margin-top: 2px;
            background: $tertiary;
            flex-shrink: 0;
            @include mobile-lg {
                margin-right: 12px;
            }
        }
        &:hover {
            text-decoration: underline !important;
        }
        p {
            @include over_text(2);
        }
    }
}
.icon-list {
    display: flex;
    @include Pad {
        flex-direction: column;
    }
    &-item {
        display: flex;
        margin-right: 16px;
        @include Pad {
            margin-bottom: 8px;
        }
        &:last-child {
            margin-right: 0;
            margin-bottom: 0;
        }
        img {
            width: 20px;
            margin-right: 8px;
        }
    }
}
/*-----------------表格樣式---------------------*/
.table-rwd {
    min-width: 100%;
    @include PC {
        min-width: 90%;
        margin: 0 auto;
    }
    thead {
        th {
            background: $base;
            color: $gray-800;
        }
    }
    th {
        display: table-cell;
        font-weight: normal;
        vertical-align: middle;
        font-size: $h6-font-size;
        padding: 8px;
        text-align: center;
        @include PC {
            display: none;
        }
    }
    td {
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        padding: 16px 8px;
        @include PC {
            display: flex;
            text-align: left !important;
            align-items: start;
            padding: 4px 20px !important;
            position: relative;
            font-size: $h7-font-size;
            &[data-th=""] {
                &::before {
                    content: "";
                    display: none;
                }
            }
        }
        &:first-child {
            @include PC {
                padding-top: 16px !important;
            }
        }
        &:last-child {
            @include PC {
                padding-bottom: 16px !important;
            }
        }
        &:before {
            display: none;
            @include PC {
                flex-shrink: 0;
                content: attr(data-th);
                display: inline-block;
                background: $base;
                color: $gray-800;
                padding: 4px 12px;
                min-width: 105px;
                border-radius: 40px;
                margin-right: 12px;
                text-align: center;
                font-weight: $font-weight-medium;
            }
        }
    }
    a {
        color: $special;
        &:hover {
            opacity: 0.8;
        }
    }
    .table-detail {
        display: none;
        @include PC {
            & > td:first-child {
                display: none;
            }
            & > td:nth-child(2) {
                background: $secondary-xlight;
                padding-bottom: 4px !important;
            }
        }
    }
    .btn{
        @include mobile-lg{
            letter-spacing: 1px !important;
        }
    }
    &[data-con="simple"] {
        thead {
            th {
                background: $white;
                font-weight: $font-weight-medium;
                padding-top: 12px;
                padding-bottom: 12px;
            }
        }
        td {
            @include PC {
                &:before {
                    min-width: auto;
                }
            }
            @include mobile-lg {
                display: flex;
                flex-direction: column;
                &:before {
                    margin-bottom: 12px;
                }
            }
        }
    }
    &[data-con="detail"] {
        @include PC {
            border-color: $secondary-xlight;
        }
        thead {
            th {
                background: $secondary-xlight;
                color: $gray-800;
            }
        }
        td {
            @include PC {
                padding-left: 0 !important;
                padding-right: 0 !important;
                background: $secondary-xlight;
                border-bottom: 1px solid $secondary;
                &:before {
                    content: attr(data-th) "：";
                    min-width: 80px;
                    background: none;
                    color: $tertiary;
                    text-align: right;
                    margin-right: 0;
                    padding: 0;
                }
                &:last-child {
                    border-bottom: none;
                }
            }
        }
        tr {
            @include PC {
                position: relative;
                &:after {
                    content: "";
                    display: block;
                    width: 100%;
                    height: 1px;
                    background: $tertiary-light;
                    position: absolute;
                    right: 0;
                    bottom: 0;
                }
                &:last-child:after {
                    display: none;
                }
            }
        }
    }
    &[data-point="xl"] {
        th {
            @include PC-lg {
                display: none;
            }
        }
        td {
            @include PC-lg {
                display: flex;
                text-align: left !important;
                align-items: start;
                padding: 4px 20px !important;
                position: relative;
                font-size: $h7-font-size;
                &[data-th=""] {
                    &::before {
                        content: "";
                        display: none;
                    }
                }
            }
            &:first-child {
                @include PC-lg {
                    padding-top: 16px !important;
                }
            }
            &:last-child {
                @include PC-lg {
                    padding-bottom: 16px !important;
                }
            }
            &:before {
                @include PC-lg {
                    flex-shrink: 0;
                    content: attr(data-th);
                    display: inline-block;
                    background: $base;
                    color: $gray-800;
                    padding: 4px 12px;
                    min-width: 95px;
                    border-radius: 40px;
                    margin-right: 12px;
                    text-align: center;
                    font-weight: $font-weight-medium;
                }
            }
        }
    }
}
.table-group {
    display: flex;
    align-items: center;
    width: 100%;
    @include mobile-md {
        flex-direction: column;
        align-items: start;
    }
}
.table-sign {
    margin: 0 auto;
    @include PC-sm {
        margin: 0;
        display: inline-block;
    }
}

/*-----------------卷軸樣式---------------------*/
.scrollbar {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $gray-300;
    }
    &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
        background-color: $gray-300;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $gray-500;
    }
}
/*-----------------表單樣式---------------------*/
.input-search {
    display: flex;
    align-items: center;
    label {
        font-size: $h5-font-size;
    }
    input {
        border-radius: 50px 0 0 50px !important;
        border: 1px solid $primary !important;
        border-right: 0 !important;
        padding-left: 20px;
        height: 45px;
        @include PC-sm {
            height: 34px;
        }
        &:focus {
            box-shadow: none;
        }
        &::placeholder {
            color: $gray-600 !important;
            @include PC-sm {
                font-size: $h7-font-size;
            }
        }
    }
    input ~ button {
        border-radius: 0 50px 50px 0 !important;
        border: 1px solid $primary;
        border-left: 0;
        background: $white !important;
        height: 45px;
        @include PC-sm {
            height: 34px;
        }
        &:hover {
            img {
                opacity: 0.8;
            }
        }
    }
}
/*-----------------視窗樣式---------------------*/
.modal {
    &-dialog {
        max-width: 600px;
    }
    &-header {
        display: flex;
        justify-content: center;
        border-bottom: none;
        position: relative;
        padding-top: 40px;
        padding-left: 24px;
        padding-right: 24px;
        .btn-close {
            background: none;
            opacity: 1;
            position: absolute;
            right: 16px;
            top: 16px;
            &:hover {
                opacity: 0.8;
            }
        }
    }
    &-title {
        font-size: $h2-font-size;
        font-weight: $font-weight-medium;
        letter-spacing: 4px;
    }
    &-content {
        border-radius: 16px !important;
    }
    &-body {
        font-size: $h6-font-size;
        @include mobile-lg {
            font-size: $h7-font-size;
        }
    }
}
/*-----------------手風琴樣式---------------------*/
.accordion {
    &-body {
        position: relative;
        @include Pad {
            font-size: $h6-font-size;
        }
        &::before {
            content: "";
            display: block;
            position: absolute;
            width: calc(100% - 32px);
            height: 1px;
            left: 16px;
            top: 0;
            background: $primary-light;
        }
    }
    &-item {
        border: none;
    }
    &-button {
        background: $white;
        color: $primary;
        border-radius: 8px !important;
        padding: 12px 48px 12px 32px;
        position: relative;
        line-height: 1.3;
        box-shadow: none !important;
        font-size: $h5-font-size;
        font-weight: $font-weight-medium;
        @include Pad {
            padding: 12px 32px 12px 18px;
        }
        &:after {
            background: url(../images/ic-add-active.svg);
            background-size: 100%;
            position: absolute;
            right: 32px;
            top: calc(50% - 10px);
            width: 24px;
            height: 24px;
            @include Pad {
                right: 16px;
                width: 18px;
                height: 18px;
                top: calc(50% - 9px);
            }
        }
        &:not(.collapsed) {
            background: $white;
            color: $secondary;
        }
        &:not(.collapsed)::after {
            background: url(../images/ic-minus-default.svg);
            background-size: 100%;
            transform: rotate(0deg);
        }
    }
}
/*-----------------頁數樣式---------------------*/

.page {
    display: flex;
    justify-content: center;
    align-items: center;
    @include mobile-lg {
        flex-direction: column;
        align-items: center;
    }
    &-list {
        display: flex;
        justify-content: flex-end;
        border-right: 1px solid $gray-300;
        padding-right: 16px;
        margin-right: 16px;
        @include mobile-lg {
            margin-bottom: 12px;
            border-right: none;
            padding-right: 0;
            margin-right: 0;
        }
    }
    &-item {
        padding: 0 4px;
        display: flex;
        align-items: center;
        &:nth-child(1),
        &:nth-child(2),
        &:nth-last-child(1),
        &:nth-last-child(2) {
            .page-link {
                text-decoration: none;
            }
        }
        .form-control {
            width: 40px;
            height: 28px;
            color: $primary;
            border: 1px solid $primary;
            border-radius: 5px;
            margin: 0 5px;
            padding: 0 8px;
            text-align: center;
        }
    }
    &-link {
        color: $gray-800;
        padding: 0;
        width: 24px;
        height: 24px;
        transition: all 0.2s ease;
        border: none;
        text-align: center;
        background: none !important;
        &:hover {
            color: $primary;
        }
        &.active {
            color: $primary;
            text-decoration: underline !important;
            background: none !important;
        }
        img {
            width: 25px;
            margin-right: 0;
        }
        &-function {
            border-radius: 4px;
        }
    }
    &-form {
        display: flex;
        align-items: center;
        flex-shrink: 0;
        color: $gray-600;
        span {
            flex-shrink: 0;
        }
        select {
            width: 64px;
            height: 28px;
            flex-shrink: 0;
            padding-top: 0;
            padding-bottom: 0;
        }
    }
}
.odd-row {
    &[data-row="5"] {
        .col-xxl-custom {
            @media (min-width: 1400px) {
                flex: 0 0 20%;
                max-width: 20%;
            }
        }
    }
}
